.App {
  text-align: center;
}

.background-wrapper {
  display: flex;
  justify-content: center;
  min-height: 100vh;
}

.page-info {
  color: #FFFFFF;
  font-family: 'Inter', sans-serif;
  text-transform: none;
  font-size: 19px;
  font-weight: 700;
  margin-bottom: 50px;
  text-align: center;
  margin-top: 10vh;
}

.ourLabel {
  color: #FFFFFF;
  font-family: 'Inter', sans-serif;
  text-transform: none;
  font-size: 19px;
  font-weight: 400;
  text-align: center;
}

.returnHeading {
  color: #FFFFFF;
  font-family: 'Inter', sans-serif;
  text-transform: none;
  font-size: 19px;
  font-weight: 400;
  text-align: center;
  margin-top: 40px;
  margin-bottom: 10px;
}

.returnSynonyms {
  color: #ccc;
  font-family: 'Inter', sans-serif;
}


.synHint {
  color: #FFFFFF;
  font-family: 'Inter', sans-serif;
  margin-bottom: 5px;
  text-align: center;
}

.loadingResults {
  color: #FFFFFF;
  font-family: 'Inter', sans-serif;
  margin-bottom: 5px;
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
